body {
  background: #7f7fd5;
  background: -webkit-linear-gradient( to right, #91eae4, #86a8e7, #7f7fd5 );
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  font-family: Caprasimo, cursive;
}

.breed-select {
  width: 350px;
  height: 50px;
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: outset #a9a9a9;
  border-radius: 10px;
  margin: 0 auto;
  font-family: inherit;
  font-size: 26px;
  line-height: 30px;
  display: block;
  position: relative;
  overflow-y: auto;
}

.breed-select option {
  background-color: #fff09e;
}

.breed-select::-webkit-scrollbar {
  width: 8px;
}

.breed-select::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
}

.breed-select::-webkit-scrollbar-thumb {
  background-color: #000;
}

.cat-info {
  width: 960px;
  background-color: #edfffd;
  border-radius: 30px;
  margin: 30px auto;
  display: flex;
  box-shadow: 0 2px 18px rgba(0, 0, 0, .3);
}

.breed-image {
  width: 500px;
  border-radius: 30px;
  padding: 15px;
}

.breed-container {
  width: 400px;
  padding: 20px;
}

.breed-name {
  color: #1d201f;
  font-size: 46px;
}

.breed-temperament {
  color: #df928e;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
}

.breed-description {
  color: #404040;
  font-size: 18px;
  line-height: 1.5;
}

.loader {
  width: 8px;
  height: 40px;
  color: #fff;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  margin: 20px auto;
  animation: animloader .3s linear .3s infinite alternate;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 8px;
  height: 40px;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  animation: animloader .3s linear .45s infinite alternate;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
}

.loader:before {
  animation-delay: 0s;
  left: -20px;
}

@keyframes animloader {
  0% {
    height: 48px;
  }

  100% {
    height: 4px;
  }
}

